*{box-sizing:border-box}body{margin:0 auto;height:100vh;display:grid;grid-template-columns:1fr;grid-template-rows:48px auto 70px;font-size:112.5%;font-family:'Quicksand', sans-serif;font-weight:300}main{overflow:auto;padding:30px;display:flex;flex-direction:column;width:100%;align-items:center}h1,h2,h3,h4,h5,h6{font-weight:500}header{display:flex;font-size:x-large;align-items:center;justify-content:center}.displaynone{display:none}ul>li{list-style:none;font-size:small;padding:5px;font-weight:550;padding:3px 10px;margin:5px 8px}footer{display:flex;flex-direction:column;justify-content:center;bottom:0}.name{font-size:x-large;text-align:center}.m-0{margin:0px}.mt-0{margin-top:0px}.mt-4{margin-top:4px}.mt-20{margin-top:20px}.mt-25{margin-top:25px}.mb-30{margin-bottom:30px}.mtb-8{margin-top:15px;margin-bottom:6px}.p-0{padding:0}.p-30{padding:30px}.mb-20{margin-bottom:20px}.bor-50{border-radius:50%}.bor-6{border-radius:6px}.bor-10{border-radius:10px}.fs-10{font-size:10px}.fs-16{font-size:16px}.dis-no{display:none}.h-15{height:25px}.z-100{z-index:100}.center{align-items:center;justify-content:center}.co-background{background-color:#FFFCF2}.co-text{color:#403D39}.co-tag{background-color:#403D39;color:#FFFCF2}.co-wtext{color:white;text-decoration:none}.co-bar{background-color:#252422}.b-shad{box-shadow:0 3px 10px #403d3992}.box{position:relative;display:flex;flex-direction:column;display:flex;border-radius:10px;margin-bottom:50px}.box__bookmark{position:absolute;top:-15px;right:40px;padding:10px;border:none;text-decoration:none;background-color:transparent}.bookmark__empty{background:url(img/bookmark-button.png) no-repeat;width:40px;height:40px}.bookmark__filled{background:url(img/bookmarked.png) no-repeat;width:40px;height:40px}.box__content--question{padding:10px 30px}.box__content--list{display:flex;flex-wrap:wrap;justify-content:center;align-items:center;margin:10px}.footer__list{margin:0;padding:0;display:flex;justify-content:space-around;align-content:center}.footer__list li{list-style:none;display:flex;flex-direction:column;justify-content:center}.footer__list li a{display:flex;display:flex;flex-direction:column;align-items:center;text-decoration:none}.create__header{text-align:center;font-size:large;font-weight:bold}.create__form--label{font-size:small}.create__form--input{width:70%;padding:10px;margin:5px 20px}.create__form--button{display:block;margin:auto;padding:10px;border-radius:6px;text-decoration:none;background-color:#EB5E28;color:#EDF2F4;margin-top:10px;font-size:large}.answer{display:flex;vertical-align:top;height:auto;justify-content:center;flex-wrap:wrap}.collapsible{padding:10px;border-radius:6px;text-decoration:none;background-color:#EB5E28;color:#EDF2F4;width:60%;border:none;text-align:center;outline:none;font-size:15px}.active,.collapsible:hover{background-color:#EB5E28}.content{padding:0;display:none;overflow:hidden;text-align:left}
